    font-size: 16px;
}

.main-content>.page-title {
    margin-bottom: 1rem;
    font-weight: 400;
}

.main-content>h4 {
    margin-top: 1.5rem;
    margin-bottom: .875rem;
}

.main-content>h4::before {
    display: block;
    height: 5.4rem;
    margin-top: -6rem;
    content: "";
}

.main-content>hr {
    margin-top: 40px;
    margin-bottom: 40px;
}

.main-content .example {
    font-size: 0.875rem;
    letter-spacing: normal;
    padding: 10px;
    background-color: #fff;
    border: 4px solid #e8ebf1;
    position: relative;
}

@media (min-width: 576px) {
    .main-content .example {
        padding: 25px;
    }
}

.main-content .highlight {
    position: relative;
    background-color: #fff;
    padding: 15px;
}

.main-content .highlight pre {
    padding: 15px;
    font-size: .875rem;
    font-family: "Overpass", sans-serif;
    background: transparent;
    line-height: 1.4;
    margin: 0;
}

.main-content .highlight pre code {
    font-family: "Overpass", sans-serif;
    padding: 0;
    tab-size: 8;
    color: #000;
    text-shadow: none;
}

.main-content .highlight pre code .token.url,
.main-content .highlight pre code .token.string,
.main-content .highlight pre code .token.entity,
.main-content .highlight pre code .token.operator {
    background: none;
}

.main-content .highlight .btn-clipboard {
    position: absolute;
    top: 6px;
    right: 6px;
    font-size: 14px;
    padding: 4px 8px;
    background: #e7ebef;
}

@media (max-width: 991px) {
    .main-content .highlight .btn-clipboard {
        padding: 2px 6px;
    }
}

.main-content .highlight .btn-clipboard:hover {
    background: #e1e6eb;
}

.main-content .highlight .btn-clipboard svg {
    width: 18px;
    height: 18px;
}

@media (max-width: 991px) {
    .main-content .highlight .btn-clipboard svg {
        width: 15px;
        height: 15px;
    }
}

.example .btn-toolbar+.btn-toolbar {
    margin-top: .5rem;
}

.example .modal.static {
    position: static;
    display: block;
}

.example .navbar {
    position: relative;
    padding: .5rem 1rem;
    left: auto;
    width: 100%;
    height: auto;
    z-index: 9;
}

.example .navbar .navbar-brand {
    font-size: 1.25rem;
}

.example .progress+.progress {
