/* Checkboxes and Radios */

.form-check {
    position: relative;
    margin-top: 10px;
    margin-bottom: 10px;
    padding-left: 0;
}

.form-check .form-check-label {
    min-height: 18px;
    display: block;
    margin-left: 1.75rem;
    font-size: 0.875rem;
    line-height: 1.5;
}

.form-check .form-check-label input {
    position: absolute;
    top: 0;
    left: 0;
    margin-left: 0;
    margin-top: 0;
    z-index: 1;
    cursor: pointer;
    opacity: 0;
    filter: alpha(opacity=0);
}

.form-check .form-check-label input[type="checkbox"]+.input-frame:before,
.form-check .form-check-label input[type="checkbox"]+.input-frame:after {
    position: absolute;
    top: 0;
    left: 0;
}

.form-check .form-check-label input[type="checkbox"]+.input-frame:before {
    content: "";
    width: 18px;
    height: 18px;
    border-radius: 2px;
    border: solid #f3e357;
    border-width: 2px;
    -webkit-transition: all;
    -moz-transition: all;
    -ms-transition: all;
    -o-transition: all;
    transition: all;
    transition-duration: 0s;
    -webkit-transition-duration: 250ms;
    transition-duration: 250ms;
}

.form-check .form-check-label input[type="checkbox"]+.input-frame:after {
    -webkit-transition: all;
    -moz-transition: all;
    -ms-transition: all;
    -o-transition: all;
    transition: all;
    transition-duration: 0s;
    -webkit-transition-duration: 250ms;
    transition-duration: 250ms;
    font-family: feather;
    content: '\e83f';
    opacity: 0;
    filter: alpha(opacity=0);
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
    font-size: .9375rem;
    font-weight: bold;
    color: #ffffff;
}

.form-check .form-check-label input[type="checkbox"]:checked+.input-frame:before {
    background: #f3e357;
    border-width: 0;
}

.form-check .form-check-label input[type="checkbox"]:checked+.input-frame:after {
    width: 18px;
    opacity: 1;
    line-height: 18px;
    filter: alpha(opacity=100);
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
}

.form-check .form-check-label input[type="checkbox"]:disabled+.input-frame:before {
    border-color: #e8ebf1;
}

.form-check .form-check-label input[type="checkbox"]:disabled:checked+.input-frame:after {
    background: #e8ebf1;
    color: #ffffff;
}

.form-check .form-check-label input[type="radio"]+.input-frame:before {
    position: absolute;
    content: "";
    top: 0;
    left: 0;
    border: solid #f3e357;
    border-width: 2px;
    width: 20px;
    height: 20px;
    border-radius: 50%;
    -webkit-transition: all;
    -moz-transition: all;
    -ms-transition: all;
    -o-transition: all;
    transition: all;
    transition-duration: 0s;
    -webkit-transition-duration: 250ms;
    transition-duration: 250ms;
}

.form-check .form-check-label input[type="radio"]+.input-frame:after {
    content: "";
    width: 8px;
    height: 8px;
    background: #ffffff;
    border-radius: 50%;
    top: 6px;
    left: 6px;
    -webkit-transition: all;
    -o-transition: all;
    transition: all;
    transition-duration: 0s;
    -webkit-transition-duration: 250ms;
    transition-duration: 250ms;
    opacity: 0;
    filter: alpha(opacity=0);
    -webkit-transform: scale(0);
    -ms-transform: scale(0);
    -o-transform: scale(0);
    transform: scale(0);
    position: absolute;
}

.form-check .form-check-label input[type="radio"]:checked+.input-frame:before {
    background: #f3e357;
    border-width: 0;
}

.form-check .form-check-label input[type="radio"]:checked+.input-frame:after {
    opacity: 1;
    line-height: 1.5;
    filter: alpha(opacity=100);
    -webkit-transform: scale(1);
    -ms-transform: scale(1);
    -o-transform: scale(1);
    transform: scale(1);
}

.form-check .form-check-label input[type="radio"]:disabled+.input-frame:before {
    border-color: #e8ebf1;
}

.form-check .form-check-label input[type="radio"]:disabled:checked+.input-frame:before {
    background: #e8ebf1;
}

.form-check .form-check-label input[type="radio"]:disabled:checked+.input-frame:after {
    background: #ffffff;
}

